import axios from "@/tools/axios/axiosConfig";
const getApi = async (props) => {
    const resp = await axios.get(props.url);
    console.log("getApi resp", props.url, resp);
    return resp.data;
};
const postApi = async (props) => {
    const resp = await axios.post(props.url, props.data);
    console.log("postApi resp", props.url, resp);
    return resp.data;
};
export { getApi, postApi };
